import { useEffect, useState } from 'react';
import axios from 'axios';
import { makeRequest } from '../axios';
import { TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper } from '@mui/material';
import { TextField } from '@mui/material';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import React from 'react';
import Fade from '@mui/material/Fade';
import { Button } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import DoneIcon from '@mui/icons-material/Done';
import '../pages/players.css';
import { useContext } from 'react';
import { AuthContext } from '../Auth/AuthContext';

const Players = ({}) => {
  const { isLoading, error, data } = useQuery(['stats'], () =>
    makeRequest.get('/stats').then((res) => {
      return res.data;
    })
  );

  const {currentUser} = useContext(AuthContext);
  const [editRow, setEditRow] = useState(null);
  const [name, setName] = useState("");
  const [kills, setKills] = useState("");
  const [deaths, setDeaths] = useState("");
  const [assists, setAssists] = useState("");

  const queryClient = useQueryClient()

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [searchQuery, setSearchQuery] = useState('');
  const [filteredData, setFilteredData] = useState(data);
  console.log(data);

  const deleteMutation = useMutation(
    (id)=>{
    return makeRequest.delete("/stats/" + id);
    },
    {
      onSuccess: () => {
          queryClient.invalidateQueries(["stats"]);
      },
    }
  );

  const handleDelete = (id) => {
    deleteMutation.mutate(id);
    if (editRow && editRow.id === id) {
      setEditRow(null);
    }
  };


  const handleEditButtonClick = (stat) => {
    setEditRow(stat);
    setName(stat.name);
    setKills(stat.kills);
    setDeaths(stat.deaths);
    setAssists(stat.assists);
  };

  const handleEditRow = () => {
    const newStats = filteredData.map((stat) => {
      if (stat.id === editRow.id) {
        return { ...stat, name, kills, deaths, assists };
      }
      return stat;
    });
    makeRequest.put('/stats/' + editRow.id, { name, kills, deaths, assists })
      .then(() => {
        queryClient.invalidateQueries(["stats"]);
      })
      .catch((error) => {
        console.log(error);
      });
    setFilteredData(newStats);
    setEditRow(null);
    setName("");
    setKills("");
    setDeaths("");
    setAssists("");
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '1px solid #f08a12',
    boxShadow: 24,
    p: 4,
  };

  useEffect(() => {
    if (!data) return;
    const filtered = data.filter((stat) =>
      stat.name.toLowerCase().includes(searchQuery.toLowerCase())
    );
    setFilteredData(filtered);
  }, [data, searchQuery]);


  const mutation = useMutation(
    (newStat) => makeRequest.post('/stats', newStat),
    {
      onSuccess: (data) => {
        queryClient.invalidateQueries(['stats']);
        setFilteredData((prevData) => [...prevData, data]);
        setName('');
        setKills('');
        setDeaths('');
        setAssists('');
        console.log(name);
      },
    }
  );

  const handleAdd = async (e) => {
    e.preventDefault();
    mutation.mutate({ name, kills, deaths, assists });
  };

  console.log(currentUser.is_admin, "ok");

  return (
    <div
    style={{
      position: 'relative',
      minHeight: '100vh', 
      overflow: 'hidden', 
    }}
  >
    <div
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        backgroundImage:
        'linear-gradient(rgba(240, 138, 18, 0.1), rgba(0, 0, 0, 0.5)), url(https://images4.alphacoders.com/997/thumb-1920-997792.jpg)',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        opacity: 0.2, 
        zIndex:'-1',
      }}
    ></div>

    <div style={{ textAlign: 'center', paddingLeft:'10%', paddingRight:'10%',
    minHeight: '90vh',  }}>

      <Typography variant="h4" sx={{mt: 5, mb: 5, color: '#f08a12', fontWeight: '600',}}>Statistics of players</Typography>
      <TextField
        type="text"
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        placeholder="Search..."
        color="warning"
        sx={{pb:3, width: '100%', maxWidth: 300, margin: 'auto'}}
      />
      {error ? (
        'Something went wrong!'
      ) : isLoading ? (
        'loading'
      ) : (
        <TableContainer component={Paper} sx={{mb:10, backgroundColor: 'rgba(255, 255, 255, 0.6)'}}>
           <Table sx={{ minWidth: 650,  }} aria-label="simple table">
            <TableHead sx={{backgroundColor:'#f08a12',}}>
              <TableRow > 
                <TableCell sx={{fontWeight:'600'}}>Name</TableCell>
                <TableCell align="right" sx={{fontWeight:'600'}}>Kills</TableCell>
                <TableCell align="right" sx={{fontWeight:'600'}}>Deaths</TableCell>
                <TableCell align="right" sx={{fontWeight:'600'}}>Assists</TableCell>
                <TableCell align="right" sx={{fontWeight:'600'}}>KDA</TableCell>
                { currentUser.is_admin && <TableCell align="right" sx={{fontWeight:'600'}}>Delete</TableCell>}
              </TableRow>
            </TableHead>
            <TableBody>
            {filteredData && filteredData.map((stat) => (
              <TableRow key={stat.id} sx={{ ':hover': { backgroundColor: 'rgba(240, 138, 18, 0.15)', boxShadow: "3px 3px 15px rgba(0,0,0,0.25)",}}}
              className={editRow && editRow.id === stat.id ? "editing" : ""}>
                  <TableCell >
                  {editRow && editRow.id === stat.id ? (
                    <TextField
                    color="warning"
                    sx={{ width: '100%', maxWidth: 270,margin: 'auto'}}
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                    />
                  ) : (
                    <Typography>{stat.name}</Typography>
                  )}
                </TableCell>
                <TableCell>
                  {editRow && editRow.id === stat.id ? (
                    <TextField
                    color="warning"
                    InputProps={{
                      inputProps: {
                        style: {
                          textAlign: 'right'}}}}
                      sx={{float: 'right', width: '100%', maxWidth: 70,margin: 'auto'}}
                      value={kills}
                      onChange={(e) => setKills(e.target.value)}
                    />
                  ) : (
                    <Typography align="right" >{stat.kills}</Typography>
                  )}
                </TableCell>
                <TableCell>
                  {editRow && editRow.id === stat.id ? (
                    <TextField
                    color="warning"
                    InputProps={{
                      inputProps: {
                        style: {
                          textAlign: 'right'}}}}
                          sx={{float: 'right', width: '100%', maxWidth: 70,margin: 'auto'}}
                      value={deaths}
                      onChange={(e) => setDeaths(e.target.value)}
                    />
                  ) : (
                    <Typography align="right" >{stat.deaths}</Typography>
                  )}
                </TableCell>
                <TableCell>
                  {editRow && editRow.id === stat.id ? (
                    <TextField
                    color="warning"
                    InputProps={{
                      inputProps: {
                        style: {
                          textAlign: 'right'}}}}
                          sx={{float: 'right', width: '100%', maxWidth: 70,margin: 'auto'}}
                      value={assists}
                      onChange={(e) => setAssists(e.target.value)}
                    />
                  ) : (
                    <Typography align="right" >{stat.assists}</Typography>
                  )}
                </TableCell>
                <TableCell align="right">
                  {stat.deaths === 0
                    ? ((stat.kills + stat.assists / 2)).toFixed(1)
                    : ((stat.kills + stat.assists / 2) / stat.deaths).toFixed(1)}
                </TableCell>
                { currentUser.is_admin &&
                <TableCell sx={{textAlign:'right'}}>
                  {editRow && editRow.id === stat.id ? (
                    <>
                    <Fab size="small" color="error" aria-label="edit" onClick={() => handleDelete(stat.id)}>
                      <DeleteForeverIcon />
                    </Fab>
                     <Fab size="small" color="success" aria-label="edit" onClick={handleEditRow} sx={{ml:0.5}} >
                      <DoneIcon />
                    </Fab>
                   </>
                  ) : (
                   <Fab size="small" color="warning" aria-label="edit" onClick={(e) => handleEditButtonClick(stat)}>
                   <EditIcon />
                    </Fab>
                  )}
                </TableCell>}
              </TableRow>
            ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

{ currentUser.is_admin &&
    <Box sx={{position: "fixed",bottom: '0', right:'0', p:9}}>
      <Fab onClick={handleOpen} sx={{ backgroundColor:'#f08a12', color:'white', transition: 'all 0.3s ease',
    ':hover': { backgroundColor: '#f29d38',}}} 
      aria-label="add">
        <AddIcon />
      </Fab> 
    </Box>
}
  <Modal
    aria-labelledby="transition-modal-title"
    aria-describedby="transition-modal-description"
    open={open}
    onClose={handleClose}
    closeAfterTransition
  >
    <Fade in={open}>
      <Box sx={style}>
        <Typography id="transition-modal-title" variant="h6" component="h2">
          Add Player
        </Typography>
        <form onSubmit={handleAdd}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="name"
            label="Name"
            name="name"
            autoFocus
            value={name}
            color='warning'
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            color='warning'
            required
            fullWidth
            id="kills"
            label="Kills"
            name="kills"
            value={kills}
            onChange={(e) => setKills(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            color='warning'
            required
            fullWidth
            id="deaths"
            label="Deaths"
            name="deaths"
            value={deaths}
            onChange={(e) => setDeaths(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            color='warning'
            required
            fullWidth
            id="assists"
            label="Assists"
            name="assists"
            value={assists}
            onChange={(e) => setAssists(e.target.value)}
          />
           
              <Button type="submit" variant="contained" color='warning' sx={{ mt: 3, mb: 2 }}>
                Save
              </Button>

        </form>
      </Box>
    </Fade>
  </Modal>
</div>
</div>
  );
};

export default Players;
