import { AuthContext } from './Auth/AuthContext';
import { createBrowserRouter, Navigate, RouterProvider, Outlet } from 'react-router-dom';
import Navbar from './components/Navbar/Navbar';
import Spodek from './components/Footer/Spodek';
import Registerform from './pages/Registerform';
import Loginform from './pages/Loginform';
import Posts from './pages/Posts';
import { useContext } from "react";
import Home from './pages/Home';
import Solo from './components/tourney_comp/solo';
import Tournaments from './pages/Tournaments';
import Players from './pages/Players';
import AboutUs from './pages/AboutUs';
import Team from './components/tourney_comp/team';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import Profile from './pages/Profile';
import MiddleTeam from './components/tourney_comp/MiddleTeam';
import MiddleSolo from './components/tourney_comp/MiddleSolo';

function App() {

  const {currentUser} = useContext(AuthContext);

  const queryClient = new QueryClient();

  const Layout = () => {
    return (
      <QueryClientProvider client={queryClient}>
      <div>
        <Navbar />
        <Outlet />
        <Spodek />
      </div>
      </QueryClientProvider>
    );
  };

  const ProtectedRoute = ({ children }) => {
    if (!currentUser) {
      return <Navigate to="/login" />;
    }

    return children;
  };

  const router = createBrowserRouter([
    {
      path: "/",
      element: (
        <ProtectedRoute>
          <Layout />
        </ProtectedRoute>
      ),
      children: [
        {
          path: "/",
          element: <Home />,
        },
        {
          path: "/Tournaments",
          element: <Tournaments />,
        },
        {
          path: "/Tournaments/5v5",
          element: <MiddleTeam />,
        },
        {
          path: "/Tournaments/5v5/:id",
          element: <Team />,
        },
        {
          path: "/tournaments/1v1",
          element: <MiddleSolo />,
        },
        {
          path: "/tournaments/1v1/:id",
          element: <Solo />,
        },
        {
          path: "/players",
          element: <Players />,
        },
        {
          path: "/posts",
          element: <Posts />,
        },
        {
          path: "About-us",
          element: <AboutUs />,
        },
        {
          path: "profile",
          element: <Profile />,
        },
        {
          path: "*",
          element: <Home />,
        },
      ],
    },
    {
      path: "/login",
      element: <Loginform />,
    },
    {
      path: "/register",
      element: <Registerform />,
    },
  ]);

  return (
    <div>
      <RouterProvider router={router} />
    </div>
  );
}
export default App;