import { useContext } from "react";
import { AuthContext } from "../../Auth/AuthContext";
import { Box, Button, Card, CardContent, Typography } from "@mui/material";
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import { useState } from "react";
import { useMutation, useQueryClient } from "@tanstack/react-query"
import { makeRequest } from "../../axios"
import MenuItem from '@mui/material/MenuItem';
import theme from '../theme';
import { ThemeProvider } from '@mui/material/styles';

const Typepost = () => {
    const [file, setFile] = useState(null);
    const [desc, setDesc] = useState("");
    const [role, setRole] = useState("");
    const [rank, setRank] = useState("");

    const ranks = [
      {
        value: 'GrandMasters',
        label: 'GrandMasters',
        img: '../img/1a.png'
      },
      {
        value: 'Masters',
        label: 'Masters',
        img: '../img/2a.png'
      },
      {
        value: 'Diamond',
        label: 'Diamond',
        img: '../img/3a.png'
      },
      {
        value: 'Platinum',
        label: 'Platinum',
        img: '../img/4a.png'
      },
      {
        value: 'Gold',
        label: 'Gold',
        img: '../img/5a.png'
      },
      {
        value: 'Silver',
        label: 'Silver',
        img: '../img/6a.png'
      },
      {
        value: 'Bronze',
        label: 'Bronze',
        img: '../img/7a.png'
      },
      {
        value: 'Unranked',
        label: 'Unranked',
        img: '../img/7b.png'
      },
    ];

    const roles =[
      {
        value: 'Support',
        label: 'Support',
        img: '../img/support.webp'
      },
      {
        value: 'ADC',
        label: 'ADC',
        img: '../img/hunter.webp'
      },
      {
        value: 'Mid laner',
        label: 'Mid laner',
        img: '../img/mage.webp'
      },
      {
        value: 'Jungler',
        label: 'Jungler',
        img: '../img/assassin.webp'
      },
      {
        value: 'Solo laner',
        label: 'Solo laner',
        img: '../img/warrior.webp'
      },
    ]

 
    const upload = async () => {
      try{
        const formData = new FormData();
        formData.append("file", file);
        const res = await makeRequest.post("/upload", formData);
        return res.data;
      }catch(err){
        console.log(err)
      }
    }

    const {currentUser} = useContext(AuthContext)

    const queryClient = useQueryClient()

    const mutation = useMutation((newPost)=>{
        return makeRequest.post('/posts', newPost);
      },
      {
        onSuccess: () => {
            queryClient.invalidateQueries(["posts"]);
        },
      }
    );

    
    const handleClick = async (e) => {
    e.preventDefault()
    let imgUrl = "";
    if(file) imgUrl = await upload();
    mutation.mutate({ role, rank, desc, img: imgUrl });
    setDesc("");
    setRank("");
    setRole("");
    setFile(null)
    }

  return (
    <ThemeProvider theme={theme}>
    <Card sx={{m:2, pl:1.5, pr:1.5, pt:3, borderRadius:3, boxShadow: "1px 1px 10px rgba(0,0,0,0.25)", backgroundColor: 'rgba(255, 255, 255, 0.7)' }}>
    <CardContent>
      <Box >
      <div style={{display:'flex'}}>
        <Typography variant="h5"  color="primary.main" sx={{mb:3, mt:-1, }}>Are you also looking for a team {currentUser.name}?</Typography>
        </div>
        <Typography variant="subtitle1"  color="grey" sx={{mb:3, mt:-3, mb:1.5 }}>Then fill out this short form!</Typography>
      <div style={{display:'flex', justifyContent:'space-between' }}>
        <TextField
          id="standard-select-currency"
          select
          label="Your current rank"
          variant="standard"
          value={rank}
          onChange={(e) => setRank(e.target.value)}
          sx={{width: '49%'}}
        >
        {ranks.map((option) => (
          <MenuItem key={option.value} value={option.value} sx={{display:'flex', alignItems:'center'}}>
            <div style={{display:'flex', alignItems:'center'}}>
            <img src={option.img} alt="" style={{width: '30px', marginRight:"6px"}} />
            <Typography variant="h7">{option.label}</Typography>
            </div>
            </MenuItem>
          ))}
        </TextField>

        <TextField
          id="standard-select-currency"
          select
          label="Your main role"
          variant="standard"
          value={role}
          onChange={(e) => setRole(e.target.value)}
          sx={{width: '49%'}}
        >
          {roles.map((option) => (
            <MenuItem key={option.value} value={option.value} sx={{display: 'flex', alignItems: 'center'}}>
              <div style={{display:'flex', alignItems:'center'}}>
              <img src={option.img} alt="" style={{width: '30px', marginRight:"6px"}} />
              <Typography variant="h7">{option.label}</Typography> 
              </div>
            </MenuItem>
          ))}
        </TextField>
        </div>

      <TextField
          id="standard-multiline-flexible"
          placeholder={` Tell us some info about you!`}
          onChange={(e) => setDesc(e.target.value)}
          value = {desc}
          multiline
          maxRows={4}
          variant="standard"
          fullWidth
          sx={{mt:3}}
        />


       <div style={{display: 'flex', alignItems: 'flex-start' }}>
        {file && <img className="file" alt="" style={{height:"100px"}} src={URL.createObjectURL(file)} />}
        {file && <Button onClick={()=>setFile(null)} sx={{fontSize:15}}>X</Button>}
        </div>
      </Box>
    <Box sx={{ display: 'flex', alignItems: 'center', mt:1 }}>
        <input type="file" id="file" style={{display:"none"}} onChange={(e) => setFile(e.target.files[0])} />
        <label htmlFor="file">
            <div style={{display: 'flex', alignItems: 'center'}}>
            <img src="https://pixsector.com/cache/517d8be6/av5c8336583e291842624.png" alt="" style={{ marginRight: '10px', width:'30px'}} />
            <span>Add Image</span>
            </div>
        </label>
        <Button variant="contained" style={{ marginLeft: 'auto' }} onClick={handleClick} >Send</Button>
    </Box>


    </CardContent>
  </Card>
  </ThemeProvider>
  )
}

export default Typepost