import { ThemeProvider } from '@emotion/react';
import { Container, Box, TextField, Button } from '@mui/material';
import Typography from '@mui/material/Typography';
import { Link } from 'react-router-dom';
import theme from '../components/theme';
import { useState, useContext, useEffect } from "react";
import { AuthContext } from '../Auth/AuthContext';
import { useNavigate } from 'react-router-dom';
import { FormControl, InputLabel, FilledInput, InputAdornment, IconButton } from '@mui/material';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';


const Loginform = () => {

  const [showPassword, setShowPassword] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };


  const [inputs, setInputs] = useState(
    {
      username: '',
      password: '',
    });

  const [err, setErr] = useState(null);

  const navigate = useNavigate()

    const handleChange = (e) => {
      setInputs((prev)=>({...prev, [e.target.name]: e.target.value }))
    }
    

  const { login } = useContext(AuthContext);

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      await login(inputs);
      console.log("login successful");
      setIsLoggedIn(true); 
    } catch (err) {
      setErr(err.response.data);
    }
  };
  
  useEffect(() => {
    if (isLoggedIn) {
      navigate("/");
    }
  }, [isLoggedIn]);

  

  return (
    <ThemeProvider theme={theme}>
    <div
    style={{
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      backgroundImage:
      'linear-gradient(rgba(240, 138, 18, 0.1), rgba(0, 0, 0, 0.5)), url(https://images4.alphacoders.com/997/thumb-1920-997792.jpg)',
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
      opacity: 0.2, 
      zIndex:'-1',
    }}
  ></div>
  
  <Container
    maxWidth="xl"
    sx={{
      minHeight: '90vh',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    }}
  >
    <Box sx={{ justifyContent: 'center', alignItems: 'center', textAlign:'center',  backgroundColor: 'rgba(255, 255, 255, 0.7)', borderRadius:4, pt:3, pr:10, pl:10, pb:1, boxShadow: "1px 1px 20px rgba(0,0,0,0.25)", mt:-10 }}>
    <Typography color="primary.main" variant="h4" sx={{mb:3}}>Login</Typography>
      <form style={{ display: 'flex', flexDirection: 'column',width:350}}>
      <TextField variant="filled" label="Username" type="text" name="username" onChange={handleChange} />
      <FormControl variant="filled">
          <InputLabel htmlFor="filled-adornment-password"  >Password</InputLabel>
          <FilledInput
           name="password" onChange={handleChange}
            id="filled-adornment-password"
            type={showPassword ? 'text' : 'password'}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
          />
        </FormControl>
      {err && err}
      <Button variant="contained" onClick={handleLogin}>Login</Button>
      </form>
    <Typography variant="body1" sx={{mt:4}} color="#363636" >Don't you have an account?</Typography>
    <Link to={'/register'}>
    <Button variant='text' sx={{mt:-4}}>Register</Button>
    </Link>
    </Box>
  </Container>
</ThemeProvider>
  );
}

export default Loginform;