import React from 'react';
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { AuthContext } from "../Auth/AuthContext";
import { useContext } from "react";
import { Email, LocationOn, Person } from '@mui/icons-material';
import { useState } from 'react';
import Update from '../components/profile/Update';
import Backdrop from '@mui/material/Backdrop';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';

const Profile = () => {

    const { currentUser } = useContext(AuthContext);

    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    console.log(currentUser)
    console.log(currentUser.is_admin)

    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
      };

  return (
    <div
    style={{
      position: 'relative',
      minHeight: '90vh',
      overflow: 'hidden',
    }}
  >
    <div
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        backgroundImage:
          'linear-gradient(rgba(240, 138, 18, 0.1), rgba(0, 0, 0, 0.5)), url(https://images4.alphacoders.com/997/thumb-1920-997792.jpg)',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        opacity: 0.2,
        zIndex: '-1',
      }}
    ></div>
    <Container maxWidth="sm" sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '2rem' }}>
      <Grid container spacing={2} justifyContent="center" >
        <Grid item>
          <Avatar
            alt="User Avatar"
            src={"/upload/"+ currentUser.profilePic}
            sx={{ width: 200, height: 200, mb:-6, boxShadow: "1px 1px 10px rgba(0,0,0,0)", border: 5, borderColor: 'rgba(255, 255, 255, 1)' }}
          />
        </Grid>
        <Grid item>
          <Box sx={{ bgcolor: 'primary.light', pl:6, pr:6, pt:5, pb:2, mb:2, borderRadius:3, boxShadow: "1px 1px 10px rgba(0,0,0,0.25)", backgroundColor: 'rgba(255, 255, 255, 1)', minWidth:300 }}>
            <Typography sx={{textAlign:"center"}} variant="h4" gutterBottom>
              {currentUser.name}
            </Typography>
            <Grid container spacing={0.8} alignItems="center" justifyContent="center" textAlign="center">
              <Grid item>
                <Person />
              </Grid>
              <Grid item >
                <Typography variant="body1" gutterBottom>
                  {currentUser.is_admin ? "Admin" : "Regular User"}
                </Typography>
              </Grid>
            </Grid>
           </Box>
            <Box sx={{ bgcolor: 'primary.light', p: 6, mb:2, borderRadius:3, boxShadow: "1px 1px 10px rgba(0,0,0,0.25)", backgroundColor: 'rgba(255, 255, 255, 1)' }}>
            <Grid container spacing={2}>
              <Grid item>
                <LocationOn />
              </Grid>
              <Grid item>
                <Typography variant="body1" gutterBottom>
                  {currentUser.country}
                </Typography>
              </Grid>
            </Grid>
            <Grid container spacing={2} >
              <Grid item>
                <Email />
              </Grid>
              <Grid item>
                <Typography variant="body1" gutterBottom>
                  {currentUser.email}
                </Typography>
              </Grid>
            </Grid>
            <Button onClick={handleOpen} variant="contained" color="warning" sx={{mt:2}}>
              Edit Profile
            </Button>
                <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={open}
                onClose={handleClose}
                closeAfterTransition
                slots={{ backdrop: Backdrop }}
                slotProps={{
                backdrop: {
                    timeout: 500,
                },
                }}
            >
                <Fade in={open}>
                <Box sx={style}>
                    <Update />
                </Box>
                </Fade>
            </Modal>
          </Box>
        </Grid>
      </Grid>
    </Container>

    </div>
  );
};

export default Profile;
