import React from 'react';
import { Card, CardContent, CardMedia, Typography, CardActions, Button } from '@mui/material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { makeRequest } from '../../axios';
import { Link } from 'react-router-dom';

const MiddleSolo = () => {

  const { isLoading, error, data } = useQuery(['tourneys'], () =>
  makeRequest.get('/tourneys').then((res) => {
    return res.data;
  })
);
console.log(data);

  return (
    <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
      {error 
        ? "Something went wrong!" 
        : isLoading 
        ? "loading"
        : data.map((tourney) =>
      
      <Card key={tourney.id} sx={{ maxWidth: 645, marginBottom: '20px' }}>
        <CardMedia
          component="img"
          height="240"
          image="https://cdna.artstation.com/p/assets/images/images/058/230/706/large/ramon-garcia-gc-s10-arch1a.jpg?1673676035"
          alt="temporary image"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {tourney.name}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas ultricies risus eu risus consectetur hendrerit.
          </Typography>
        </CardContent>
        <CardActions>
        <Link to={`/Tournaments/1v1/${tourney.id}`}>
          <Button size="small"> Go to the tournament</Button>
          </Link>
        </CardActions>
      </Card>
      )}
    </div>
  );
}

export default MiddleSolo;
