import { ThemeProvider } from '@emotion/react';
import { Container, Box, Button } from '@mui/material';
import TextField from '@mui/material/TextField/TextField';
import Typography from '@mui/material/Typography';
import {Link } from 'react-router-dom';
import theme from '../components/theme';
import { useState } from 'react';
import axios from 'axios';
import Alert from '@mui/material/Alert';

const Registerform = () => {
  
  const [inputs, setInputs] = useState(
    {
      username: '',
      email: '',
      password: '',
      name: '',
    }
  );

  const [err, setErr] = useState(null);
  const [successMsg, setSuccessMsg] = useState(null);

    const handleChange = (e) => {
      setInputs((prev)=>({...prev, [e.target.name]: e.target.value }))
    }

    const handleClick = async e => {
      e.preventDefault();
      
          // checkuje name
      if (inputs.username.length < 6) {
        setErr(
          <Alert severity="error">Name must be at least 6 letters long.</Alert>
        );
        return;
      }

          // checkuje mail
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(inputs.email)) {
        setErr(<Alert severity="error">Invalid email address.</Alert>);
        return;
      }
      
      // checkuje heslo
      const passwordRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%^&+=!*()_\-\\[\]{}|:;"'<>,.?/~])\S{8,}$/;
      if (!passwordRegex.test(inputs.password)) {
        setErr(<Alert severity="error">Password must contain at least one uppercase letter, one lowercase letter, and one number.</Alert>);
        return;
      }

      // checkuje potvrzen hesla
      if (inputs.password !== inputs.passwordConfirmation) {
      setErr(
        <Alert severity="error">Passwords do not match.</Alert>
      );
      return;
      }

      // checkuje name
      if (inputs.name.length < 6) {
        setErr(
          <Alert severity="error">Name must be at least 6 letters long.</Alert>
          );
        return;
      }
      

      try {
        await axios.post('http://localhost:8800/api/auth/register', inputs)
        setSuccessMsg(<Alert severity="success">Registration successful! Now you can login!</Alert>);
      } catch (err) {
        setErr(err.response.data);
      }
    }

  return (
    <ThemeProvider theme={theme}>
        <div
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          backgroundImage:
          'linear-gradient(rgba(240, 138, 18, 0.1), rgba(0, 0, 0, 0.5)), url(https://images4.alphacoders.com/997/thumb-1920-997792.jpg)',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          backgroundRepeat: 'no-repeat',
          opacity: 0.2, 
          zIndex:'-1',
        }}
      ></div>
      
      <Container
        maxWidth="xl"
        sx={{
          minHeight: '90vh',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        <Box sx={{ justifyContent: 'center', alignItems: 'center', textAlign:'center', backgroundColor: 'rgba(255, 255, 255, 0.7)', borderRadius:4, pt:3, pr:10, pl:10, pb:1, boxShadow: "1px 1px 20px rgba(0,0,0,0.25)", mt:-10 }}>
        <Typography color="primary.main" variant="h4" sx={{mb:3}}>Register</Typography>
          <form style={{ display: 'flex', flexDirection: 'column',width:350 }}>
          <TextField variant="filled" label="Username" type="text" name="username" onChange={handleChange} required />
          <TextField variant="filled" label="Email" type="email" name="email" onChange={handleChange} required />
          <TextField variant="filled" label="Password" type="Password" name="password" onChange={handleChange} required />
          <TextField variant="filled" label="Confirm Password" type="Password" name="passwordConfirmation" onChange={handleChange} required />
          <TextField variant="filled" label="Name" type="text" name="name" onChange={handleChange} required />
          {err && err}
          {successMsg && successMsg}
          <Button variant="contained" onClick={handleClick} >Register</Button>
          </form>
        <Typography variant="body1" sx={{mt:4}} color="#363636" >Do you have an account?</Typography>
        <Link to={'/login'}>
        <Button variant='text' sx={{mt:-4}}>Login</Button>
        </Link>
        </Box>
      </Container>
    </ThemeProvider>
  );
};

export default Registerform;