import { Box, Card, CardContent, Typography } from "@mui/material";
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import MessageIcon from '@mui/icons-material/Message';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import Comments from "./comments";
import Avatar from '@mui/material/Avatar';
import moment from "moment";
import { useContext } from "react";
import {AuthContext} from '../../Auth/AuthContext';
import { makeRequest } from '../../axios'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import Button from '@mui/material/Button';
import { useState } from "react";
import './post.css';

const Post = ({ post }) => { 

  const [commentOpen, setCommentOpen] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  const {currentUser} = useContext(AuthContext);

  const { isLoading, error, data = [] } = useQuery(['likes', post.id], () => 
  makeRequest.get("/likes?post_id="+post.id).then((res) => {
    return res.data;
  }));

  const {data: commentsData = [] } = useQuery(['comments', post.id], () => 
    makeRequest.get("/comments?post_id="+post.id).then((res) => {
      return res.data;
    }));

  const queryClient = useQueryClient()

  const mutation = useMutation(
    (liked)=>{
    if (liked) return makeRequest.delete('/likes?post_id='+ post.id);
    return makeRequest.post("/likes", {post_id: post.id});
    },
    {
      onSuccess: () => {
          queryClient.invalidateQueries(["likes"]);
      },
    }
  );


  const deleteMutation = useMutation(
    (post_id)=>{
    return makeRequest.delete("/posts/" + post_id);
    },
    {
      onSuccess: () => {
          queryClient.invalidateQueries(["posts"]);
      },
    }
  );
  
  const handleLike = () => {
    mutation.mutate(data.includes(currentUser.id))

  }
  
  const handleDelete = () => {
    deleteMutation.mutate(post.id)
  }

  function getImageRank(rank) {
    if (rank === 'GrandMasters') {
      return '../img/1a.png'
    } else if (rank === 'Masters') {
      return '../img/2a.png'
    } else if (rank === 'Diamond') {
      return '../img/3a.png'
    } else if (rank === 'Platinum') {
      return '../img/4a.png'
    } else if (rank === 'Gold') {
      return '../img/5a.png'
    } else if (rank === 'Silver') {
      return '../img/6a.png'
    } else if (rank === 'Bronze') {
      return '../img/7a.png'
    } else if (rank === 'Unranked') {
      return '../img/7b.png'
    }
  }

  function getImageRole(rank) {
    if (rank === 'Support') {
      return '../img/support.webp'
    } else if (rank === 'ADC') {
      return '../img/hunter.webp'
    } else if (rank === 'Mid laner') {
      return '../img/mage.webp'
    } else if (rank === 'Jungler') {
      return '../img/assassin.webp'
    } else if (rank === 'Solo laner') {
      return '../img/warrior.webp'
    } 
  }

  return (
    
    <Card sx={{m:2, pl:1.5, pr:1.5, pt:1, borderRadius:3, boxShadow: "1px 1px 10px rgba(0,0,0,0.25)", backgroundColor: 'rgba(255, 255, 255, 0.7)' }}>
      <CardContent>
        <Box sx={{display:'flex',}}>
        <Avatar src={"/upload/"+ post.profilePic} sx={{ width: 60, height: 60 }}/>

        <div style={{marginLeft:'10px'}}>
          <div style={{display:'flex', marginBottom:'-7px'}}>
        <Typography variant="h6" component="h2" sx={{mr:10}}>{post.name}</Typography>
        <div className="rankCSS" style={{ boxShadow: post.rank === 'GrandMasters' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.rank === 'Masters' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.rank === 'Diamond' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.rank === 'Platinum' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.rank === 'Gold' ? '1px 1px 2px rgba(0,0,0,0.25)': post.rank === 'Silver' ? '1px 1px 2px rgba(0,0,0,0.25)': post.rank === 'Bronze' ? '1px 1px 2px rgba(0,0,0,0.25)' : 'none'
         ,backgroundColor: post.rank === 'GrandMasters' ? '#bffaf9' : post.rank === 'Masters' ? '#dabffa' : post.rank === 'Diamond' ? '#bfd9fa' : post.rank === 'Platinum' ? '#bffad0' : post.rank === 'Gold' ? '#eae77a': post.rank === 'Silver' ? '#cdcdcd': post.rank === 'Bronze' ? '#ecc172' : 'none'}}>
          <img src={getImageRank(post.rank)} alt="" style={{height:"25px", marginRight:'5px'}} /> 
          <Typography variant="subtitle1" component="p" sx={{mr:3, display:{ xs: 'none', md: 'flex' }}}>{post.rank}</Typography>
        </div>
        <div className="roleCSS" style={{boxShadow: post.role === 'Support' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.role === 'ADC' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.role === 'Mid laner' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.role === 'Jungler' ? '1px 1px 2px rgba(0,0,0,0.25)' : post.role === 'Solo laner' ? '1px 1px 2px rgba(0,0,0,0.25)' : 'none',
            backgroundColor: post.role === 'Support' ? '#b9e8c6' : post.role === 'ADC' ? '#ecc172' : post.role === 'Mid laner' ? '#ddb9e8' : post.role === 'Jungler' ? '#eae77a' : post.role === 'Solo laner' ? '#e8b9b9' : 'none'}}>
            <img src={getImageRole(post.role)} alt="" style={{height:"25px", marginRight:'5px'}} />
            <Typography variant="subtitle1" component="p" sx={{display:{ xs: 'none', md: 'flex' }}}>{post.role}</Typography>
          </div>
        </div>
        <Typography variant="subtitle2" component="p" color="textSecondary">
          {moment(post.createdAt).fromNow()}
        </Typography>
        </div>

        <div style={{marginLeft: 'auto', cursor:'pointer'}}>
         <MoreHorizIcon onClick={()=>setMenuOpen(!menuOpen)}/>
         {menuOpen && post.user_id === currentUser.id && ( <Button variant="outlined" onClick={handleDelete} sx={{position:'absolute',mt:3.5, ml:-10}} >Delete</Button>)}
        </div>
        </Box>

        <Box sx={{mt:1 }}>
        <Typography variant="body2" component="p" sx={{fontSize:17}}>{post.desc}</Typography>
        <img src={"./upload/"+post.img} alt="" style={{width:'100%', marginTop:10, borderRadius:'15px'}}/>
        </Box>
        
        <Box sx={{display:'flex', alignItems:'center', mb:-2, pt:2}}>
        <div style={{display:'flex', cursor: 'pointer'}}  onClick={handleLike}>

         {isLoading ? "loading" : data.includes(currentUser.id) ? (
        <FavoriteIcon sx={{color:'#f08a12'}} /> ) : ( <FavoriteBorderIcon />)
         } 

        <Typography sx={{ml:1, mr:3}}>{data.length} Recommends </Typography>
        </div>
        <div style={{display:'flex', cursor: 'pointer'}} onClick={()=>setCommentOpen(!commentOpen)}>
        <MessageIcon />
        <Typography sx={{ml:1}}>{commentsData.length} Comments</Typography>

        </div>

        </Box>


      </CardContent>
      {commentOpen && <Comments post_id={post.id}/>}
    </Card>


  )
}

export default Post;