import React from "react";

const data = [
  {
    round: 1,
    matches: [
      {
        id: 1,
        player1: "Player 1",
        player2: "Player 8",
        winner: "Player 1",
      },
      {
        id: 2,
        player1: "Player 4",
        player2: "Player 5",
        winner: null,
      },
      {
        id: 3,
        player1: "Player 3",
        player2: "Player 6",
        winner: null,
      },
      {
        id: 4,
        player1: "Player 2",
        player2: "Player 7",
        winner: null,
      },
    ],
  },
  {
    round: 2,
    matches: [
      {
        id: 5,
        player1: "Player 1",
        player2: null,
        winner: null,
      },
      {
        id: 6,
        player1: null,
        player2: null,
        winner: null,
      },
    ],
  },
  {
    round: 3,
    matches: [
      {
        id: 7,
        player1: null,
        player2: null,
        winner: null,
      },
    ],
  },
];

class MiddleTeam extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: data,
    };
  }

  handleClick = (match, winner) => {
    const newData = this.state.data.slice();
    const round = newData.find((round) => round.round === match.round);
    const matchIndex = round.matches.findIndex(
      (m) => m.id === match.id
    );

    round.matches[matchIndex].winner = winner;

    if (match.round < this.state.data.length) {
      const nextRound = newData.find(
        (round) => round.round === match.round + 1
      );
      const nextMatchIndex = Math.floor(matchIndex / 2);

      if (matchIndex % 2 === 0) {
        nextRound.matches[nextMatchIndex].player1 = winner;
      } else {
        nextRound.matches[nextMatchIndex].player2 = winner;
      }
    }

    this.setState({
      data: newData,
    });
  };

  renderMatch = (match) => {
    const winner = match.winner;
    const player1 = match.player1;
    const player2 = match.player2;
    const winnerStyle = winner ? "winner" : "";
    const disabled = winner ? "disabled" : "";

    return (
      <div className={`match ${winnerStyle}`} key={match.id}>
        <div className={`player ${disabled}`} onClick={() => this.handleClick(match, player1)}>
          {player1}
        </div>
        <div className={`player ${disabled}`} onClick={() => this.handleClick(match, player2)}>
          {player2}
        </div>
      </div>
    );
  };

  renderRound = (round) => {
    return (
      <div className="round" key={round.round}>
        {round.matches.map((match) => {
          return this.renderMatch(match);
        })}
      </div>
    );
  };

  render() {
    return (
      <div className="bracket">
        {this.state.data.map((round) => {
          return this.renderRound(round);
        })}
      </div>
    );
  }
}

export default MiddleTeam;
