import { TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import { Button } from '@mui/material'
import { AuthContext } from '../../Auth/AuthContext'
import { useContext } from 'react'
import { useMutation, useQueryClient } from "@tanstack/react-query"
import { makeRequest } from "../../axios"
import {Box} from '@mui/material'

const Update = () => {


    const { currentUser, updateUser } = useContext(AuthContext);

    const [image, setImage] = useState(null);
    const [texts, setTexts] = useState({
      name: currentUser.name,
      country: currentUser.country,
      email: currentUser.email,
    });

    console.log(image);

    const upload = async (file) => {
        console.log(file)
        try {
          const formData = new FormData();
          formData.append("file", file);
          const res = await makeRequest.post("/upload", formData);
          return res.data;
        } catch (err) {
          console.log(err);
        }
      };

      
    const handleChange = (e) => {
        setTexts((prev) => ({ ...prev, [e.target.name]: [e.target.value] }));
      };    

    const queryClient = useQueryClient();

    const mutation = useMutation(
        (currentUser) => {
          return makeRequest.put("/users", currentUser);
        },
        {
          onSuccess: () => {
            queryClient.invalidateQueries(["user"]);
          },
        }
      );

    const handleSubmit = async (e) => {
        e.preventDefault();

        let profileUrl;
        profileUrl = image ? await upload(image) : currentUser.profilePic;

        mutation.mutate({ ...texts, profilePic: profileUrl });
        const updatedUser = { ...currentUser, ...texts, profilePic: profileUrl };
        updateUser(updatedUser); 
      };

  return (
    <div>
      <Typography id="transition-modal-title" variant="h6" component="h2">
        Update profile
      </Typography>
      <form>
        <TextField
          variant="outlined"
          margin="normal"
          fullWidth
          color="warning"
          id="name"
          label="Name"
          name="name"
          value={texts.name}
          onChange={handleChange}
          autoFocus
        />
        <TextField
          variant="outlined"
          margin="normal"
          fullWidth
          color="warning"
          id="country"
          label="Country"
          name="country"
          value={texts.country}
          onChange={handleChange}
           />
        <TextField
          variant="outlined"
          margin="normal"
          color="warning"
          fullWidth
          id="Email"
          label="Email"
          name="email"
          value={texts.email}
          onChange={handleChange}
           />
        <div style={{display: 'flex', alignItems: 'flex-start' }}>
            <img className="file" alt=""  src="" />
        </div>
        <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
        <label htmlFor="image">
              <span>Profile Picture</span>
              <div className="imgContainer">
                <img
                  src={image ? URL.createObjectURL(image) : "/upload/" + currentUser.profilePic}
                  alt=""
                  style={{height:'100px'}}
                />
                <img src="https://pixsector.com/cache/517d8be6/av5c8336583e291842624.png" alt="" style={{ marginRight: '10px', width: '30px' }} />
              </div>
            </label>
            <input
              type="file"
              id="image"
              style={{ display: "none" }}
              onChange={(e) => setImage(e.target.files[0])}
            />
          
        </Box>
        <Button onClick={handleSubmit} variant="contained" color='warning' sx={{ mt: 3, mb: 2 }}>
          Save
        </Button>
        
      </form>
    </div>
  )
}

export default Update
