import { Avatar, Button } from '@mui/material';
import Typography from '@mui/material/Typography';
import { useContext } from 'react'
import {AuthContext} from '../../Auth/AuthContext';
import { TextField } from '@mui/material';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { makeRequest } from '../../axios'
import moment from "moment";
import { useState } from 'react';



const Comments = ({post_id}) => {

  const [desc, setDesc] = useState("");
  const {currentUser} = useContext(AuthContext);

  const { isLoading, error, data  } = useQuery(['comments'], () => 
  makeRequest.get("/comments?post_id="+post_id).then((res) => {
  return res.data;
}));

const queryClient = useQueryClient()

const mutation = useMutation((newComment)=>{
    return makeRequest.post('/comments', newComment);
  },
  {
    onSuccess: () => {
        queryClient.invalidateQueries(["comments"]);
    },
  }
);

const handleClick = async (e) => {
e.preventDefault();
mutation.mutate({desc, post_id})
setDesc("");
}

  return (
    <div style={{borderTop: "1px solid #585858"}}>
      <div style={{display:'flex', alignItems:'center', justifyContent:'center'}}>
        <Avatar src={"/upload/"+ currentUser.profilePic} sx={{ width: 45, height: 45, marginBottom:2}}/>
        <TextField  id="outlined-multiline-flexible" multiline maxRows={4} label="Write a comment" variant="outlined" fullWidth sx={{m:2}} 
        onChange={(e) => setDesc(e.target.value)} 
        value={desc}/>
        <Button sx={{p:2, mb:2}} variant="outlined" onClick={handleClick}> Send </Button>

      </div>

       {isLoading ? "loading" : data.map((comment) => (
        <div style={{ display: 'flex', flexDirection: 'row', marginBottom: 15, marginLeft:30, marginRight:60 }}>
      <div>
        <Avatar src={"/upload/"+ comment.profilePic} sx={{ width: 45, height: 45, marginTop:1 }} />
      </div>
        <div style={{ marginLeft: 15 }}>
          <Typography variant="h6" component="h2">{comment.name}</Typography>
          <Typography sx={{mb:0, mt:-0.7}}>{comment.desc}</Typography>
          <Typography variant="caption" >{moment(comment.createdAt).fromNow()}</Typography>
        </div>
      </div>
       ))}
    </div>
  )
}

export default Comments;